export default {
	name: 'login',
	props: {
		title: String
	},
	methods: {
		login () {
			this.$http({
				url: this.$urlPath.userInfoLoginUrl,
				loadingTip: '正在登录',
				params: {
					account: 'admin',
					password: '123456'
				},
				onRequestSuccess: (res) => {
					this.$userInfo.saveUserInfo(res.data.userinfo, res.data.communities)
				},
				onRequestFail: (errorCode, error) => {
					console.log(error)
				}
			})
		}
	}
}
